:root {
  --sectionPT: 50px;
  --sectionPB: 0;
  --container: 15px;
  --paddingTop: 40px;
  --paddingBot: 40px;
}
@media (min-width: 768px) {
  :root {
    --sectionPT: 70px;
    --container: 24px;
    --paddingTop: 60px;
    --paddingBot: 60px;
  }
}
@media (min-width: 1920px) {
  :root {
    --sectionPT: 110px;
    --container: 290px;
  }
}
html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}
body {
  min-width: 360px;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 1.8rem;
  color: #333333;
  background-color: #ffffff;
  margin: 0;
}
@media (min-width: 768px) {
  body {
    font-size: 16rem;
    line-height: 2.4rem;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}
.container {
  padding: 0 var(--container);
  margin: 0 auto;
  box-sizing: border-box;
}
h1 {
  font-size: 3.4rem;
  line-height: 3.7rem;
  font-weight: 700;
  color: #333333;
}
@media (min-width: 768px) {
  h1 {
    font-size: 5rem;
    line-height: 5.5rem;
  }
}
@media (min-width: 1920px) {
  h1 {
    font-size: 7rem;
    line-height: 7.7rem;
  }
}
h2 {
  font-size: 3.4rem;
  line-height: 4.1rem;
  font-weight: 500;
  color: #333333;
}
@media (min-width: 768px) {
  h2 {
    font-size: 5.4rem;
    line-height: 6.5rem;
  }
}
h3 {
  font-size: 2.6rem;
  line-height: 3.4rem;
  font-weight: 500;
  color: #333333;
}
img {
  display: block;
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
}
p,
h1,
h2,
h3,
ul,
ol,
li,
button {
  padding: 0;
  margin: 0;
}
ul {
  list-style-type: none;
}
fieldset {
  border: none;
  padding: 0;
  margin: 0;
}
a {
  display: block;
  text-decoration: none;
  text-transform: none;
  color: inherit;
  box-sizing: border-box;
}
button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  box-sizing: border-box;
}
p {
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.6rem;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff");
  font-display: swap;
}
.header__link {
  display: flex;
  align-items: center;
}
.header__logo {
  width: 60px;
  height: 60px;
}
.header__logo-text {
  display: flex;
  flex-direction: column;
  font-size: 1.1rem;
  line-height: 1.4rem;
  font-weight: 500;
}
.menu__toggle {
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.menu__toggle span {
  display: block;
  opacity: 1;
  width: 20px;
  height: 2px;
  background-color: #333333;
  transition: 0.3s;
}
.menu__toggle--open span:first-child {
  transform: translate(0, 8px) rotate(45deg);
  transition: 0.3s;
}
.menu__toggle--open span:last-child {
  transform: translate(0, -8px) rotate(-45deg);
  transition: 0.3s;
}
.menu__toggle--open span:nth-child(2) {
  opacity: 0;
  transition: 0.3s;
}
